<template>
    <header id="header">
    <div class="left">
        <h1><a href="./index.html" title="title"><span>{{dataTitle.title1}}</span> <span>{{dataTitle.title2}}</span></a></h1>
    </div>
    <div class="right">
        <nav id="nav">
            <ul>
                <li @mouseleave="mouseOff()" class="col" v-for="item,idx in 카테고리" :key="item.메인메뉴">
                    <a @mouseenter="mouseOn(idx)" href="#" :class="['main-btn', {'on':isOn[idx]}]" :title="item.메인메뉴" @click.prevent="eventEmitMethod(item.메인메뉴)">{{item.메인메뉴}}</a>
                    <div :class="['sub', 'sub'+(idx+1), {'on':isOn[idx]}]">
                        <div class="sub-container">
                            <ul>
                                <li v-for="item2 in item.서브메뉴" :key="item2.서브메뉴">
                                    <ul>
                                        <li v-for="row in item2" @click.prevent="subEventMethod(row)" :key="row"><a href="#" :title="row">{{row}}</a></li>
                                    </ul>
                                </li>
                            </ul>
                        </div>
                    </div>
                </li>
            </ul>
        </nav>
    </div>
</header>
</template>

<script>
export default {
    name:'HeaderComponent',
    props:['dataTitle'],
    data(){
        return{
            카테고리:[
                {'메인메뉴' : '탑', "서브메뉴" : [['블라우스', "티", '셔츠', '니트'], ['자켓' ,'코트','가디건' ,'머플러'], [ '청바지', '짧은바지', '긴바지', '레깅스'],['귀고리', '목걸이', '반지', '팔찌']]},
                {'메인메뉴' : '아우터', "서브메뉴" : [['블라우스', "티", '셔츠', '니트'], ['자켓' ,'코트','가디건' ,'머플러'], [ '청바지', '짧은바지', '긴바지', '레깅스'],['귀고리', '목걸이', '반지', '팔찌']]},
                {'메인메뉴' : '팬츠', "서브메뉴" : [['블라우스', "티", '셔츠', '니트'], ['자켓' ,'코트','가디건' ,'머플러'], [ '청바지', '짧은바지', '긴바지', '레깅스'],['귀고리', '목걸이', '반지', '팔찌']]},
                {'메인메뉴' : '악세서리', "서브메뉴" : [['블라우스', "티", '셔츠', '니트'], ['자켓' ,'코트','가디건' ,'머플러'], [ '청바지', '짧은바지', '긴바지', '레깅스'],['귀고리', '목걸이', '반지', '팔찌']]},
            ],
            isOn: new Array(4).fill(false)
        }

    },
    methods:{
        mouseOn(idx){
            this.isOn=Array(4).fill(false);
            this.isOn[idx]=true;
        },
        mouseOff(){
            this.isOn=Array(4).fill(false);
        },
        eventEmitMethod(z){
            this.$emit('eventEmit', z );
        },
        subEventMethod(z){
            this.$emit('subEmit', z );
        }
    }
}
</script>

<style>
#wrap #header {width: 100%;height: 100px;display: flex;position: relative;z-index: 3;}
#wrap #header .left {width: 200px;height: 100%;display: flex;align-items: center;}
#wrap #header .left h1 {width:100%;height: 40px;}
#wrap #header .left h1 a {
    font-size: 24px;
    font-weight: 600;
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}
#wrap #header .left h1 a span:nth-child(1) {
    color: #e09;
}
#wrap #header .left h1 a span:nth-child(2) {
    margin-left: 5px;
    color: #f0e;
}

#wrap #header .right {width: 1000px;height: 100%;}
/* 네비게이션 */
#wrap #header .right #nav {
    width:700px;margin:49px 20px 0px auto; 
}
#wrap #header .right #nav > ul {display: flex;width:100%;}
#wrap #header .right #nav > ul > li {
    width:25%;
    border-top:1px solid #f0e;
    border-bottom:1px solid #f0e;
    border-left:1px solid #f0e;
    overflow: hidden;
}
#wrap #header .right #nav > ul > li:last-child {border-right:1px solid #f0e;}

#wrap #header .right #nav > ul > li > a {
    width: 100%;display: flex;align-items: center;justify-content: center;
    height: 50px;
    font-size: 15px;
    font-weight: 600;
    color: #000;
    transition: all 0.3s;
    background: #fff;
    transition: all 0.5s;
}
#wrap #header .right #nav > ul > li > a:hover {
    background: #f0e;
    color: #fff;
    transform: scale(1.5);
}   
#wrap #header .right #nav > ul > li > a.on {
    background: #f0e;
    color: #fff;
    transform: scale(1.5);
}   

/* 서브메뉴 */


#wrap #header .right #nav > ul > li > .sub {position: absolute; top: 100px; left: 0; width: 100%; background: #f0e;display: none;}
#wrap #header .right #nav > ul > li > .sub.on {display: block;}
#wrap #header .right #nav > ul > li > .sub .sub-container {width: 700px; margin: 0 20px 0 auto; padding: 20px 0;}
#wrap #header .right #nav > ul > li > .sub .sub-container > ul {width: 100%; display: flex;}
#wrap #header .right #nav > ul > li > .sub .sub-container > ul > li {width: 25%;}
#wrap #header .right #nav > ul > li > .sub .sub-container > ul > li ul {width: 100%;}
#wrap #header .right #nav > ul > li > .sub .sub-container > ul > li ul li {width: 100%; overflow: hidden;}
#wrap #header .right #nav > ul > li > .sub .sub-container > ul > li ul li a {color: #fff; width: 100%; height: 30px; display: flex; justify-content: center; align-items: center; transition: all 0.5s;}

#wrap #header .right #nav > ul > li > .sub .sub-container > ul > li ul li a:hover {background: #fff; color: #f0e; transform: scale(1.5);}
</style>