<template>
<div id="wrap">
    <div id="wrap">
            <header-component :data-title="dataTitle" @eventEmit="eventEmitMethod" @subEmit="subEmit"></header-component>
            <main-component></main-component>
            <footer-component></footer-component>
            <modal-component></modal-component>
        </div>
</div>
</template>

<script>
import FooterComponent from './wrap/FooterComponent.vue'
import HeaderComponent from './wrap/HeaderComponent.vue'
import MainComponent from './wrap/MainComponent.vue'
import ModalComponent from './wrap/ModalComponent.vue'
export default {
    name:"WrapComponent",
    props:[],
    components:{
        'header-component' : HeaderComponent,
        'main-component' : MainComponent,
        'footer-component' : FooterComponent,
        'modal-component' : ModalComponent,
    },
    data(){
        return{
            dataTitle:{
                title1:'JSUT',
                title2:'쇼핑몰',
            },
        }
    },
    methods:{
        eventEmitMethod(z){
            this.dataTitle.title2 = z;
            console.log(z)
        },
        subEmit(z){
            this.dataTitle.title1= z;
            console.log(z)
        }
    }
}
</script>

<style>
#wrap {width: 1200px;margin: 0 auto;}
</style>