<template>
    <section id="section2">
            <div class="left">
                <div class="container">
                    <div class="title">
                        <button @click.prevent="onClickNotice" :class="['notice-btn',{'on':isOn}]">{{noticeBtn}}</button>    
                        <button @click.prevent="onClickGallery" :class="['gallery-btn',{'on':isOn}]">{{galleryBtn}}</button>    
                    </div>
                    <div class="content">
                        <div v-show="isNotice" class="notice-box">
                            <ul>
                                <li v-for="item,idx in dataState.공지사항" :key="item.내용"><a @click.prevent="onClickPopUp(idx)" href="#" class="popup-btn" :title="item.내용">{{item.내용}}</a><span>{{item.날짜}}</span></li>
                            </ul>
                        </div>
                        <div v-show="isGallery" class="gallery-box">
                            <ul>
                                <li v-for="item in dataState.갤러리" :key="item.title"><a href="#" :title="item.title"><img :src="item.imgSrc" :alt="item.title"></a></li>
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
            <div class="center">
                <div class="container">
                    <div class="title hide"><h2>이벤트 배너</h2></div>
                    <div class="content">
                        <a href="#" :title="eBanner"><h3>{{eBanner}}</h3></a>
                    </div>
                </div>
            </div>
            <div class="right">
                <div class="container">
                    <div class="title hide"><h2>바로가기 배너</h2></div>
                    <div class="content">
                        <ul>
                            <li v-for="item in 배너" :key="item.title"><a href="#" :title="item.title"><span>{{item.title}}</span></a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </section>
</template>

<script>
import EventBus from '../../../eventBus.js'
export default {
    name:'Section2Component',
    props:['dataState'],
    data(){
        return{
            noticeBtn:'공지사항',
            galleryBtn:'갤러리',
            배너:[
                {title:'할인정보'},
                {title:'이벤트정보'},
                {title:'고객정보'},
            ],
            eBanner:'여름 마지막 세일',
            isNotice: true,
            isGallery: false,
            isOn: false,
        }
    },
    methods:{
        onClickGallery(){
            this.isNotice = false;
            this.isGallery = true;
            this.isOn = true;
        },
        onClickNotice(){
            this.isNotice = true;
            this.isGallery = false;
            this.isOn = false;
        },
        onClickPopUp(idx){
            // EventBus.$emit('이벤트이름',매개변수)
            EventBus.$emit('ponePopUp',idx)
        }
    }
}
</script>

<style>
#wrap #main #section2 {width: 100%;height: 200px;display: flex;padding: 10px 0;user-select: none;}
#wrap #main #section2 .left   {width: 33.333%;height: 100%;}
#wrap #main #section2 .left  .container {width: 100%;height: 100%;position: relative;padding-top: 35px;}

/* 공지사항 & 갤러리 박스 */
#wrap #main #section2 .left .container .title {
    width: 100%;
    height: 36px;
    display: flex;
    padding: 0 0 0 10px;
    position: absolute;
    top:0;
    left:0;
    z-index: 2;
}
#wrap #main #section2 .left   .container .title button {padding: 0 10px;border: 0;border-radius: 5px 5px 0 0;height: 36px;}

/* 공지사항버튼 */
#wrap #main #section2 .left   .container .title button.notice-btn {border: 1px solid #f0e;border-bottom: 1px solid transparent;background: #fff;cursor: default;}
#wrap #main #section2 .left   .container .title button.notice-btn.on {border: 1px solid #f0e;background: #f3f3f3;cursor: pointer;}

/* 갤러리버튼 */
#wrap #main #section2 .left   .container .title button.gallery-btn {border: 1px solid #f0e;background: #f3f3f3;cursor: pointer;}
#wrap #main #section2 .left   .container .title button.gallery-btn.on {border: 1px solid #f0e;border-bottom: 1px solid rgba(204,204,204, 0);background: #fff;cursor: default;}

/* 콘텐츠(목록 내용) 공지사항 내용 & 갤러리 내용 */
#wrap #main #section2 .left   .container .content {
    width: 100%;
    height: 145px;
    background: #fff;
    border: 1px solid #f0e;
    position: relative;
    z-index: 1;
}
#wrap #main #section2 .left   .container .content > div {
    width: 100%;
    height: 100%;
    padding: 0 10px;
    background: #fff;
}

#wrap #main #section2 .left   .container .content   div.notice-box {
    padding-top: 8px;
}
#wrap #main #section2 .left   .container .content   div.notice-box ul {width:100%;}
#wrap #main #section2 .left   .container .content   div.notice-box ul li {
    width:100%;
    line-height: 24.6px;
    border-bottom: 1px dotted #eee;
    padding-left: 13px;
    position: relative;
    display: flex;
    justify-content: space-between; 
}
#wrap #main #section2 .left   .container .content   div.notice-box ul li:last-child {border-bottom: 0;}
#wrap #main #section2 .left   .container .content   div.notice-box ul li::before {
    content: '';
    width: 2px;
    height: 2px;
    background: #333;
    position: absolute;
    top: 50%;
    left: 0;
    margin-top: -1px;
}

#wrap #main #section2 .left   .container .content   div.notice-box ul li a {
    font-size: 14px;
    width: 270px;
    overflow: hidden;
    display: block;
    white-space: nowrap;
    text-overflow: ellipsis;
}
#wrap #main #section2 .left   .container .content   div.notice-box ul li a:hover {text-decoration: underline;color:#90f}

#wrap #main #section2 .left   .container .content   div.notice-box ul li span {color: #999;font-size: 13px;}


#wrap #main #section2 .left   .container .content   div.gallery-box {
    background:#fff;    
}
#wrap #main #section2 .left   .container .content   div.gallery-box ul {
    width:100%;
    height: 100%;
    display: flex;
    align-items: center;
}
#wrap #main #section2 .left   .container .content   div.gallery-box ul li {width:33.333%;}
#wrap #main #section2 .left   .container .content   div.gallery-box ul li a {margin:0 10px;display: block;overflow: hidden;}
#wrap #main #section2 .left   .container .content   div.gallery-box ul li a img {width:100%;transition: all 0.3s;}
#wrap #main #section2 .left   .container .content   div.gallery-box ul li a img:hover {transform: scale(1.1);}

/* 이벤트 배너 */
#wrap #main #section2 .center {width: 33.333%;height: 100%;background: #fff;padding: 0 0 0 10px;}
#wrap #main #section2 .center .container {width: 100%;height: 100%;border: 0px solid #ccc;}
#wrap #main #section2 .center .container .content {width: 100%;height: 100%;}
#wrap #main #section2 .center .container .content a {
    width: 100%;height: 100%;display: flex;justify-content: center;align-items: center;
    background: url(./images/image4.jpg) no-repeat 50% 50%;
    background-size: cover;
    border: 1px solid #e09;
}
#wrap #main #section2 .center .container .content a h3 {
    font-size: 24px;font-weight: 600;
    line-height: 50px;
    background: rgba(240, 100,200, 0.5);
    color: #fff;
    width: 100%;
    text-align: center;
}
#wrap #main #section2 .center .container .content a:hover h3 {color: #fff;}

/* 바로가기 배너 */
#wrap #main #section2 .right  {width: 33.333%;height: 100%;background: #fff;padding: 0 0 0 10px;}
#wrap #main #section2 .right .container {width: 100%;height: 100%;border: 0px solid #ccc;}
#wrap #main #section2 .right .container .content {width: 100%;height: 100%;}
#wrap #main #section2 .right .container .content ul {
    width: 100%;
    height: 100%;
    display: flex;
    flex-flow: column nowrap;
    justify-content: space-between;
}
#wrap #main #section2 .right .container .content ul li {width: 100%;}
#wrap #main #section2 .right .container .content ul li a {
    display: flex;
    width:100%;
    height: 55px;
    align-items: center;
    justify-content: center;
    background: url() no-repeat 50% 50%;
    background-size: cover;
    font-size: 17px;
    font-weight: 600;
    border: 1px solid #e09;
}

#wrap #main #section2 .right .container .content ul li:nth-child(1) a {
    background-image: url(./images/image2.jpg);
}
#wrap #main #section2 .right .container .content ul li:nth-child(2) a {
    background-image: url(./images/image.jpg);
}
#wrap #main #section2 .right .container .content ul li:nth-child(3) a {
    background-image: url(./images/image3.jpg);
}
</style>