<template>
    <section id="section1">
            <div class="slide-container">
                <div class="slide-content">
                    <ul class="slide-wrap">
                        <li class="slide slide3"><a href="#" title="JUST쇼핑몰 9월 세일 이벤트 안내"><span>JUST쇼핑몰 9월 세일 이벤트 안내</span></a></li>
                        <li v-for="item,i in 슬라이드" :class="['slide', 'slide'+(i+1)]" :key="item.타이틀"><a href="#" :title="item.타이틀"><span>{{item.타이틀}}</span></a></li>
                        <li class="slide slide1"><a href="#" title="JUST 쇼핑몰 오픈!"><span>JUST 쇼핑몰 오픈!</span></a></li>
                    </ul>
                </div>
            </div>
        </section>
</template>

<script>
import $ from 'jquery'
export default {
    name:'Section1Component',
    props:[],
    data(){
        return{
            슬라이드:[
                {타이틀:'JUST 쇼핑몰 오픈!'},
                {타이틀:'종합 쇼핑의 새 막을 연다! JUST 쇼핑몰!'},
                {타이틀:'JUST쇼핑몰 9월 세일 이벤트 안내'},
            ]
        }
    },
    created(){
        let cnt=0;

        function mainSlide(){ 
            $('.slide-wrap').stop().animate({left:`${-100*cnt}%`}, 600,function(){
                if(cnt>2) cnt=0;
                $('.slide-wrap').stop().animate({left:`${-100*cnt}%`}, 0);
            });
        }

        function mextCount(){
            cnt++;
            mainSlide();
        }

        function autotimer(){
            setInterval(mextCount, 3000);
        }
        autotimer();

    }
}
</script>

<style>
#wrap #main #section1 {width: 100%;height: 300px;background-color: #f0c;}

#wrap #main #section1 .slide-container {width:100%;}
#wrap #main #section1 .slide-container .slide-content {width:100%; height: 300px; overflow: hidden;}
#wrap #main #section1 .slide-container .slide-content .slide-wrap {
    width: 500%;
    margin-left: -100%;
    position: relative;
    display: flex;
    left: 0%;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide {
    width: 100%;
    height: 300px;
    background: url() no-repeat 50% 50%;
    display: flex;
    align-items: center;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide.slide1 {
    background-image: url(./images/banner_img.jpg);
    background-position: 50% 30%;
    background-size: 120% auto;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide.slide2 {
    background-image: url(./images/banner_img2.jpg);
    background-position: 50% 0%;
    background-size: 100% auto;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide.slide3 {
    background-image: url(./images/banner_img3.png);
    background-position: 50% 30%;
    background-size: 110% auto;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide a {width: 100%; height: 100%; display: flex; align-items: center;}

#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide a span {
    width: 100%;
    height: 70px;
    background: rgba(200, 10, 150, 0.5);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 28px;
    color:#fff;
    user-select: none;
}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide2 a span {background-color: rgba(204, 40, 180, 0.5);}
#wrap #main #section1 .slide-container .slide-content .slide-wrap .slide3 a span {background-color: rgba(190, 20, 200, 0.5);}
</style>